import React, {useEffect, useState} from 'react';
import {View, Panel, PanelHeader, Div, Separator, Avatar,
    Card, CardGrid, Group, Header, Spinner} from '@vkontakte/vkui';
import axios from 'axios';
import config from './../config';
let LeaderUser = props => (
    <div style={{padding: 10}}>
        <div style={{display: 'flex'}}>
            <div style={{flex: 1, height: '40px', fontSize: '25px', display: 'flex', justifyContent: 'center', alignItems: 'center',marginRight: 10}}>
                {props.user.position}
            </div>
            <div style={{flex: 2, marginRight: 10}}>
                <Avatar src={`${props.user.photo}`} size={40}/>
            </div>
            <div style={{flex: 5, display: 'flex', alignItems: 'center',fontWeight: 600 }}>
                {props.user.first_name} {props.user.last_name}
            </div>
            <div style={{flex: 1.5,  display: 'flex', justifyContent: 'flex-end', alignItems: 'center',fontWeight: 600, color: '#2975CC', marginRight: 5}}>
                {props.user.countKarma}
            </div>
        </div>
        <div style={{marginTop: 10}}>
            <Separator/>
        </div>
    </div>
)
let Leader = ({id, fetchedUser}) => {
    let [leaders, setLeaders] = useState(null);
    let [userPlace, setUserPlace] = useState(null);
    useEffect(() => {
        axios.get(`${config.domain}/getLeaders`).then(data => {
            console.log('LEADERS = ', data)
            setLeaders(data.data.leaders);
            for (let i = 0; i < data.data.leaders.length; i++ ){ 
                if (data.data.leaders[i].id == fetchedUser.id) {
                    setUserPlace(i + 1);
                    break;
                }
            }
        });
    }, []);
    return (
        <View id={id} activePanel={id}>
			<Panel id={id}  style={{position: 'relative', height: 'calc(100vh - 70px)'}}>
                <PanelHeader >Таблица лидеров</PanelHeader>
                <Group header={<Header mode='secondary'>По уровню</Header>}>
                    {/* <Div> */}
                        {/* <CardGrid>
                            <Card size='1' mode='shadow' style={{width: '100%', minHeight: 200, marginBottom: 50, borderRadius: 5}}>
                                <LeaderUser
                                    user = {{
                                        first_name : 'Саша',
                                        last_name : 'Жабович',
                                        photo : 'https://sun9-3.userapi.com/c639822/v639822055/55abd/Y4nIaWtNYVI.jpg',
                                        countKarma : 20000,
                                        position: 1
                                    }}
                                />
                                <LeaderUser
                                    user = {{
                                        first_name : 'Илья',
                                        last_name : 'Беляев',
                                        photo : 'https://sun9-61.userapi.com/c850428/v850428872/14f10b/DuBd53-Om6c.jpg',
                                        countKarma : 1500,
                                        position: 2
                                    }}
                                />
                                <LeaderUser
                                    user = {{
                                        first_name : 'Виктория',
                                        last_name : 'Вадимовна',
                                        photo : 'https://sun9-10.userapi.com/c851420/v851420604/5bd3f/w5n_viq3MDo.jpg',
                                        countKarma : 200,
                                        position: 3
                                    }}
                                />
                                  <LeaderUser
                                    user = {{
                                        first_name : 'Иван',
                                        last_name : 'Петров',
                                        photo : 'https://upload.wikimedia.org/wikipedia/ru/8/86/%D0%98%D0%B2%D0%B0%D0%BD_%D0%98%D0%B2%D0%B0%D0%BD%D0%BE%D0%B2%D0%B8%D1%87_%D0%9F%D0%B5%D1%82%D1%80%D0%BE%D0%B2_%28%D0%BF%D0%B5%D0%B2%D0%B5%D1%86%29.jpg',
                                        countKarma : 100,
                                        position: 4
                                    }}
                                />
                                  
                                
                            </Card>
                        </CardGrid> */}
                    {leaders ? 
                    // <Div>
                        <CardGrid style={{margin: 0}}>
                            <Card size='1' mode='shadow' style={{width: '100%', minHeight: 200, marginBottom: 50, borderRadius: 5}}>
                                {leaders.map((e, index) => {
                                     if (index <= 9) return  <LeaderUser
                                     user = {{
                                         first_name : e.first_name,
                                         last_name : e.last_name,
                                         photo : e.photo,
                                         countKarma : e.level,
                                         position: index + 1
                                     }} 
                                     /> 
                                     else return;
                                })}
                            </Card>
                        </CardGrid>
                    // </Div>
                    :
                        <Spinner size='medium' style={{marginTop: 40}}/>
                    }
                    {/* </Div> */}
                </Group>
                <Div style={{position: 'absolute', bottom: 0, left: 0, width: 'calc(100% - 32px)', textAlign: 'center', fontWeight: 'bold'}}>
                Ваше место: <span style={{fontSize: '25px', color: '#2975CC', marginRight:'5px'}}>{userPlace}</span>
                    </Div>
            </Panel>
		</View>
    )
}

export default Leader;